<template>
    <div class="info-container">
        <ul class="nav nav-tabs">
            <li class="nav-item">
                <a class="nav-link"
                   :class="{active: listType === 1}"
                   @click="$emit('list-type-change', 1)">全馆浏览</a>
            </li>
            <li class="nav-item">
                <a class="nav-link"
                   :class="{active: listType === 2}"
                   @click="$emit('list-type-change', 2)">我的借阅</a>
            </li>
        </ul>

        <Loading v-if="false"/>
        <div class="empty-text" v-else-if="user.name === null && listType === 2">
            <a href="" @click="$router.push({path: '/login'})">登录</a> 后即可查看“我的借阅”信息
        </div>
        <div class="empty-text" v-else-if="books === null || books.length === 0">
            暂无相关书目信息
        </div>
        <BookBoard v-else
                   :books="books"
                   :cur-page="curPage"
                   :total-page="totalPage"
                   @page-change="pageChange"
                   @borrow-book="borrowBook"
                   @return-book="returnBook"/>
    </div>
</template>

<script>
    import Loading from "@/components/index/info/Loading"
    import BookBoard from "@/components/index/info/BookBoard"

    export default {
        name: "Info",
        components: {BookBoard, Loading},
        props: ["loading", "user", "books", "listType", "curPage", "totalPage"],

        methods: {
            pageChange(page) {
                this.$emit('page-change', page)
            },

            borrowBook(bid) {
                this.$emit('borrow-book', bid)
            },

            returnBook(bid) {
                this.$emit('return-book', bid)
            }
        }
    }
</script>

<style scoped>
    .info-container {
        margin: 8px 0;
    }

    @media (min-width: 768px) {
        .info-container {
            width: 70%;
            display: inline;
            float: left;
        }
    }

    .nav {
        font-size: 14px;
    }

    .empty-text {
        margin: 16px auto;
        text-align: center;
        font-size: 18px;
        font-weight: 300;
    }
</style>
