import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from "@/components/index/Index"
import Login from "@/components/login/Login"
import Reg from "@/components/reg/Reg"

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: Index
    },
    {
        path: '/login',
        component: Login
    },
    {
        path: '/reg',
        component: Reg
    }
]

export default new VueRouter({routes, mode: "history"})
