            <button type="button" class="btn btn-header btn-header-login" @click="$emit('logout')">退出</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Header",
        props: ["user"],

        created() {
            this.$emit('check-cur-user')
        }
