<template>
    <div class="col-se">
        <a href="javascript:void(0)" @click="option(book.option, book.id)">
            <div class="book-card">
                <div class="book-card-name">{{ book.name }}</div>
                <div class="book-card-intro">{{ book.intro }} </div>
                <table>
                    <tr><td>作者：</td><td>{{ book.author }}</td></tr>
                    <tr><td>热度：</td><td>{{ book.hot }}</td></tr>
                    <tr><td>上架时间：</td><td>{{ book.time }}</td></tr>
                </table>
                <div class="book-card-option" v-if="book.option === 1">
                    (点击卡片 <b class="option-borrow">借阅</b> 本书)
                </div>
                <div class="book-card-option" v-else-if="book.option === 2">
                    (点击卡片 <b class="option-return">归还</b> 本书)
                </div>
                <div class="book-card-option" v-else-if="book.option === 3">
                    (此书 <b class="option-no">暂无库存</b> 不可借阅)
                </div>
                <div class="book-card-option" v-else>
                    (请先 <b class="option-login">登录</b> 以进行更多操作)
                </div>
            </div>
        </a>
    </div>
</template>

<script>
    export default {
        name: "BookCard",
        props: ["book"],

        methods: {
            option(oi, bid) {
                if (oi === 1) {
                    this.$emit('borrow-book', bid)
                } else if (oi === 2) {
                    this.$emit('return-book', bid)
                }
            }
        }
    }
</script>

<style scoped>
    a { color:black; }
    a:hover { color:black; text-decoration: none; }
    a:focus { color:black; text-decoration: none; }
    a:active { color:black; text-decoration: none; }
    a:visited { color:black; text-decoration: none; }

    table {
        margin: 8px 0;
    }

    tr {
        border-top: 1px dashed #42b983;
        border-bottom: 1px dashed #42b983;
    }

    td {
        padding: 4px;
        font-size: 14px;
    }

    .book-card {
        padding: 8px;
        border: 1px solid #d1d5da;
        border-radius: 3px;
        height: 100%;
    }

    .book-card-name {
        padding: 4px;
        font-size: 18px;
        font-weight: 700;
        color: #42b983;
    }

    .book-card-option {
        width: 100%;
        margin-top: 8px;
        text-align: center;
        font-size: 14px;
    }

    .book-card-intro {
        padding: 4px;
        font-size: 16px;
        color: darkgray;
        text-indent: 2em;
    }

    .option-borrow {
        color: #5cb85c;
    }

    .option-return {
        color: #5bc0de;
    }

    .option-no {
        color: #d9534f;
    }

    .option-login {
        color: #42b983;
    }

    .col-se {
        width: 100%;
        padding: 8px;
    }

    @media (min-width: 690px) {
        .col-se {
            width: 50%;
        }
    }

    @media (min-width: 1050px) {
        .col-se {
            width: 33.3333333%;
        }
    }
</style>
