<template>
    <div class="container">
        <Header :user="user"
                @logout="logout"
                @check-cur-user="checkCurUser"/>
        <Option @search="search"
                @sort-type-change="sortTypeChange"/>
        <Info :loading="loading"
              :user="user"
              :books="books"
              :list-type="listType"
              :cur-page="curPage"
              :total-page="totalPage"
              @page-change="pageChange"
              @list-type-change="listTypeChange"
              @borrow-book="borrowBook"
              @return-book="returnBook"/>
    </div>
</template>

<script>
    import Header from "@/components/index/Header"
    import Option from "@/components/index/Option"
    import Info from "@/components/index/info/Info"

    export default {
        name: "Index",
        components: {Info, Option, Header},

        data() {
            return {
                user: {name: null},
                sortType: 1,
                listType: 1,
                curPage: 1,
                totalPage: 1,
                searchText: "",
                loading: true,
                books: []
            }
        },

        methods: {
            checkCurUser() {
                fetch("api/user/cur" , {
                    credentials: "include"
                }).then((res) => res.json()).then((data) => {
                    this.user = data
                })
            },

            logout() {
                fetch("api/account/logout", {
                    credentials: "include"
                }).then(() => {
                    location.reload()
                })
            },

            search(text) {
                this.curPage = 1
                this.searchText = text
                this.refreshBookList()
            },

            sortTypeChange(type) {
                this.sortType = type
                this.refreshBookList()
            },

            listTypeChange(type) {
                this.listType = type
                this.refreshBookList()
            },

            pageChange(page) {
                this.curPage = page
                this.refreshBookList()
            },

            borrowBook(bid) {
                fetch("api/book/borrow" , {
                    credentials: "include",
                    body: "bid=" + bid,
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then((res) => res.json()).then((data) => {
                    let msg = "借阅成功"
                    if (data.code === 1) {
                        this.refreshBookList()
                    } else {
                        msg = data.msg;
                    }
                    alert(msg)
                })
            },

            returnBook(bid) {
                fetch("api/book/return" , {
                    credentials: "include",
                    body: "bid=" + bid,
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then((res) => res.json()).then((data) => {
                    let msg = "归还成功"
                    if (data.code === 1) {
                        this.refreshBookList()
                    } else {
                        msg = data.msg;
                    }
                    alert(msg)
                })
            },

            refreshBookList() {
                this.loading = true
                const bodyStr =
                    "page=" + this.curPage +
                    "&sortType=" + this.sortType +
                    "&listType=" + this.listType +
                    "&search=" + this.searchText
                // console.log(bodyStr)
                fetch("api/book/list" , {
                    credentials: "include",
                    body: bodyStr,
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then((res) => res.json()).then((data) => {
                    this.totalPage = data.pages
                    this.books = data.books
                    this.loading = false
                })
            }
        },

        created() {
            this.refreshBookList()
        }
    }
</script>

<style scoped>
    .container {
        width: 100%;
        margin: 0;
        max-width: 4096px;
    }
</style>
