<template>
    <div class="book-board">
        <div class="row">
            <BookCard v-for="book in books"
                      :key="book.id" :book="book"
                      @borrow-book="borrowBook"
                      @return-book="returnBook"/>
        </div>
        <div class="pages">
            <ul class="pagination justify-content-center">
                <li class="page-item" v-for="n in totalPage"
                    :key="n" :class="{active: n === curPage}"
                    @click="$emit('page-change', n)">
                    <a class="page-link">{{ n }}</a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import BookCard from "@/components/index/info/BookCard";
    export default {
        name: "BookBoard",
        components: {BookCard},
        props: ["books", "curPage", "totalPage"],

        methods: {
            borrowBook(bid) {
                this.$emit('borrow-book', bid)
            },

            returnBook(bid) {
                this.$emit('return-book', bid)
            }
        }
    }
</script>

<style scoped>
    .book-board {
        padding: 0 16px;
    }

    .pagination a {
        color: #42b983!important;
    }

    .pagination .active a {
        color: white!important;
        background-color: #42b983!important;
        border-color: #42b983!important;
    }

    .pages .pagination {
        margin: 8px 0;
    }
</style>
