<template>
    <div>
        <Logo/>
        <div class="reg-text">加入Void Library</div>
        <div class="reg-adv">(广告位招租)</div>
        <div class="alert alert-danger" v-if="errorMsg !== ''">{{ errorMsg }}</div>
        <form class="reg-form">
            <div class="form-group">
                <label for="username">用户名:</label>
                <input type="text" class="form-control" id="username" v-model="username">
            </div>
            <div class="form-group">
                <label for="pwd">密码:</label>
                <input type="password" class="form-control" id="pwd" v-model="pwd">
            </div>
            <div class="form-group">
                <label for="rep">重复密码:</label>
                <input type="password" class="form-control" id="rep" v-model="rep">
            </div>
            <button type="button" class="btn btn-primary btn-reg" @click="reg">注册</button>
        </form>
    </div>
</template>

<script>
    import Logo from "@/components/common/Logo"

    export default {
        name: "Reg",
        components: {Logo},

        data() {
            return {
                errorMsg: '',
                username: '',
                pwd: '',
                rep: ''
            }
        },

        methods: {
            reg() {
                this.errorMsg = ''
                if (this.username === '') {
                    this.errorMsg = "用户名不能为空"
                    return
                }
                if (this.pwd === '') {
                    this.errorMsg = "密码不能为空"
                    return
                }
                if (this.rep === '') {
                    this.errorMsg = "请重复密码"
                    return
                }
                if (this.pwd !== this.rep) {
                    this.errorMsg = "两次密码输入不一致"
                    return
                }
                fetch("api/account/reg" , {
                    credentials: "include",
                    body: "username=" + this.username + "&password=" + this.pwd,
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then((res) => res.json()).then((data) => {
                    if (data.code === 0) {
                        this.errorMsg = data.msg
                    } else {
                        if (data.type === 'admin') {
                            this.$router.push({path: "/admin"})
                        } else {
                            this.$router.push({path: "/"})
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .reg-text {
        font-size: 24px;
        font-weight: 300;
        width: 100%;
        text-align: center;
        margin: 16px 0;
    }

    .reg-adv {
        font-size: 14px;
        color: #999999;
        width: 100%;
        text-align: center;
    }

    .alert {
        margin: 8px auto;
        width: 350px;
        text-align: center;
        padding: 8px;
    }

    .reg-form {
        width: 350px;
        padding: 16px;
        margin: 16px auto;
        border: 1px solid #d8dee2;
        border-radius: 5px;
    }

    .btn-reg {
        margin-top: 16px;
        background-color: #4fc08d;
        width: 100%;
        border-color: #42b983;
    }

    .btn-reg:hover {
        background-color: #4fc08d;
    }

    .btn-reg:focus {
        background-color: #4fc08d;
    }
</style>
