<template>
    <div>
        <Logo/>
        <div class="login-text">登录Void Library</div>
        <div class="login-adv">(广告位招租)</div>
        <div class="alert alert-danger" v-if="errorMsg !== ''">{{ errorMsg }}</div>
        <form class="login-form">
            <div class="form-group">
                <label for="username">用户名:</label>
                <input type="text" class="form-control" id="username" v-model="username">
            </div>
            <div class="form-group">
                <label for="pwd">密码:</label>
                <input type="password" class="form-control" id="pwd" v-model="pwd">
            </div>
            <button type="button" class="btn btn-primary btn-login" @click="login">登录</button>
        </form>
        <div class="login-reg">
            没有账号？
            <a href="" @click="$router.push({path: '/reg'})">
                点击注册
            </a>
        </div>
    </div>
</template>

<script>
    import Logo from "@/components/common/Logo"

    export default {
        name: "Login",
        components: {Logo},

        data() {
            return {
                errorMsg: '',
                username: '',
                pwd: ''
            }
        },

        methods: {
            login() {
                this.errorMsg = ''
                if (this.username === '') {
                    this.errorMsg = "用户名不能为空"
                    return
                }
                if (this.pwd === '') {
                    this.errorMsg = "密码不能为空"
                    return
                }
                fetch("api/account/login" , {
                    credentials: "include",
                    body: "username=" + this.username + "&password=" + this.pwd,
                    method: "POST",
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    }
                }).then((res) => res.json()).then((data) => {
                    if (data.code === 0) {
                        this.errorMsg = data.msg
                    } else {
                        if (data.type === 'admin') {
                            this.$router.push({path: "/admin"})
                        } else {
                            this.$router.push({path: "/"})
                        }
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .login-text {
        font-size: 24px;
        font-weight: 300;
        width: 100%;
        text-align: center;
        margin: 16px 0;
    }

    .login-adv {
        font-size: 14px;
        color: #999999;
        width: 100%;
        text-align: center;
    }

    .alert {
        margin: 8px auto;
        width: 350px;
        text-align: center;
        padding: 8px;
    }

    .login-form {
        width: 350px;
        padding: 16px;
        margin: 16px auto;
        border: 1px solid #d8dee2;
        border-radius: 5px;
    }

    .btn-login {
        margin-top: 16px;
        background-color: #4fc08d;
        width: 100%;
        border-color: #42b983;
    }

    .btn-login:hover {
        background-color: #4fc08d;
    }

    .btn-login:focus {
        background-color: #4fc08d;
    }

    .login-reg {
        width: 350px;
        margin: 24px auto;
        padding: 8px 0;
        text-align: center;
        border: 1px solid #d8dee2;
        border-radius: 5px;
        font-size: 14px;
    }
</style>
